import React from 'react'
import styles from './Cards.module.css';
import CountUp from 'react-countup';

const Cards = ({data: {confirmed, recovered, deaths, lastUpdate}}) => {
    if(!confirmed){
        return 'Loading...';
    }
    return (
        <div className={styles.cards_main} >
            <div className={styles.cards_1}>
                <div className={styles.cards_1_main}>
                    <div className={styles.cards_1_name}><p>Infected</p></div>
                    <div className={styles.cards_1_value}><p>
                        <CountUp 
                            start={0}
                            end={confirmed.value}
                            duration={2.5}
                            separator=","
                        />
                    </p>
                    </div>
                    <div className={styles.cards_1_date}><p>{new Date(lastUpdate).toDateString()}</p></div>
                    <div className={styles.cards_1_discription}><p>Number of Active cases of COVID-19</p></div>
                    <div className={styles.cards_1_line}><p></p></div>
                </div>
            </div>
            <div className={styles.cards_2}>
                <div className={styles.cards_2_main}>
                    <div className={styles.cards_2_name}><p>Recovered</p></div>
                    <div className={styles.cards_2_value}><p>
                         <CountUp 
                                 start={0}
                                 end={recovered.value}
                                 duration={2.5}
                                 separator=","
                         />
                    </p></div>
                    <div className={styles.cards_2_date}><p>{new Date(lastUpdate).toDateString()}</p></div>
                    <div className={styles.cards_2_discription}><p>Number of Recovered cases of COVID-19</p></div>
                    <div className={styles.cards_2_line}><p></p></div>
                </div>
            </div>
            <div className={styles.cards_3}>
                <div className={styles.cards_3_main}>
                    <div className={styles.cards_3_name}><p>Death</p></div>
                    <div className={styles.cards_3_value}><p>
                        <CountUp 
                            start={0}
                            end={deaths.value}
                            duration={2.5}
                            separator=","
                        />
                    </p></div>
                    <div className={styles.cards_3_date}><p>{new Date(lastUpdate).toDateString()}</p></div>
                    <div className={styles.cards_3_discription}><p>Number of Death cases of COVID-19</p></div>
                    <div className={styles.cards_3_line}><p></p></div>
                </div>
            </div>                
        </div>
    )
}

export default Cards
